<template>
  <div>
    <button
      type="button"
      class="btn btn-sm"
      data-bs-toggle="modal"
      data-bs-target="#exampleVerticallycenteredModal"
    >
      Tags
    </button>
    <div
      class="modal fade"
      id="exampleVerticallycenteredModal"
      tabindex="-1"
      aria-hidden="true"
      style="display: none"
    >
      <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Tags</h5>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div class="modal-body">
            <div class="mb-3">
              <div class="bootstrap-tagsinput" @click="$refs.input.focus()">
                <span
                  class="badge bg-primary"
                  v-for="(tag, i) in tags"
                  :key="tag"
                  >{{ tag
                  }}<span data-role="remove" @click="removeTag(i)"></span></span
                ><input
                  @blur="addTag(input)"
                  v-model="input"
                  type="text"
                  :size="input.length"
                  ref="input"
                  @keydown.enter="addTag(input)"
                />
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-bs-dismiss="modal"
            >
              Close</button
            ><button type="button" class="btn btn-primary">Save</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      input: "",
      tags: [],
      cats: ["other", "oracle", "java"],
    };
  },
  props: {},
  methods: {
    addTag(input) {
      let tag = input.trim();
      if (tag.length) {
        if (this.tags.indexOf(tag) == -1) {
          this.tags.push(tag);
        }
        this.input = "";
      }
    },
    removeTag(i) {
      this.tags.splice(i, 1);
    },
  },
};
</script>

<style scoped></style>
