import store from "store";

const PAGE_SIZE = 100;

function getStatus() {
  return (
    store.get("status") || {
      next: 0,
      total: 0,
    }
  );
}

export function save(link, r) {
  let id = link.id;
  let st = getStatus();

  if (r || !id) {
    for (let i = 0; i <= st.total / PAGE_SIZE; i++) {
      let items = pageItems("links", i);
      if (items.some((e) => e && e.url == link.url)) return;
    }
    id = (st.total++, st.next++);
    if (!link.r) {
      let news = store.get("_n") || [];
      news.push(id);
      store.set("_n", news);
    }
  }
  let pageNo = parseInt(id / PAGE_SIZE);
  let items = pageItems("links", pageNo);
  link.id = id;
  items[id % PAGE_SIZE] = link;
  pageItems("links", pageNo, items);

  store.set("status", st);
}
function getItem(obj, id) {
  let pageNo = parseInt(id / PAGE_SIZE);
  let items = pageItems(obj, pageNo);

  return items[id % PAGE_SIZE];
}

function pageItems(obj, pn, items) {
  let pid = "_" + obj + pn;
  if (items) store.set(pid, items);
  else {
    console.log("get page" + pn);
    return store.get(pid) || [];
  }
}

export function search(obj, pageNo, keywork) {
  if (!keywork) {
    let page = pageItems(obj, (pageNo * 10) / PAGE_SIZE);
    return page.slice(pageNo * 10, pageNo * 10 + 10).filter((e) => e);
  }
}
const fetchRequest = (url, params = {}, timeout = 10000) => {
  let isTimeout = false;

  return new Promise(function (resolve, reject) {
    const TO = setTimeout(function () {
      isTimeout = true;
      reject(new Error("Fetch timeout"));
    }, timeout);

    fetch(url, params)
      .then((res) => {
        clearTimeout(TO);
        if (!isTimeout) {
          resolve(res);
        }
      })
      .catch((e) => {
        if (isTimeout) {
          return;
        }
        reject(e);
      });
  });
};
export async function getBlobContent(name, rep, path, sha) {
  let token = store.get("token");
  var auth = "token " + token;

  let headers = {
    accept: "application/vnd.github.v3+json",
  };
  if (token) headers["Authorization"] = auth;
  let url =
    `https://api.github.com/repos/${name}/${rep}/contents/${path}` +
    (sha ? `?ref=${sha}` : "");

  let json = await fetchRequest(
    url,
    {
      method: "get",
      headers: headers,
      // cache: "force-cache",
    },
    10000
  ).then((r) => r.json());

  return json.content;
}

async function forEachCommitsAsc({ name, rep, path, since }, cb) {
  var commitsUrl = `https://api.github.com/repos/${name}/${rep}/commits?path=${encodeURIComponent(
    path
  )}&per_page=100`;
  if (since) commitsUrl += "&since=" + encodeURIComponent(since);

  let token = store.get("token");
  var auth = "token " + token;

  let headers = {
    accept: "application/vnd.github.v3+json",
  };
  if (token) headers["Authorization"] = auth;
  let curPage = 1;
  let commits = [];
  for (;;) {
    let pageCommits = await fetch(commitsUrl + "&page=" + curPage, {
      method: "get",
      headers: headers,
    }).then((response) => {
      if (response.status == 200) return response.json();
      else throw response;
    });
    commits.unshift(...pageCommits.reverse());
    if (pageCommits.length < 100) break;
    curPage++;
  }

  for (let i = 0; i < commits.length; i++) {
    let commit = commits[i];
    let content = await getBlobContent(name, rep, path, commit.sha);

    if (await cb(commit, b64_to_utf8(content), i)) return;
  }
  return commits;
}
function utf8_to_b64(str) {
  return btoa(unescape(encodeURIComponent(str)));
}
function b64_to_utf8(base64) {
  return decodeURIComponent(escape(atob(base64)));

  // return atob(str);
}
async function upload(name, rep, path, content) {
  let token = store.get("token");
  var auth = "token " + token;
  var url = `https://api.github.com/repos/${name}/${rep}/contents/${path}`;

  if (!token)
    return {
      error: "token?",
    };
  let json = await fetch(url, {
    method: "get",
    headers: {
      accept: "application/vnd.github.v3+json",
    },
  }).then((r) => r.json());
  let sha = json.sha;
  json = await fetch(url, {
    method: "put",
    body: JSON.stringify({
      message: "update" + new Date(),
      branch: "main",
      content: utf8_to_b64(content),
      sha: sha,
    }),
    headers: {
      accept: "application/vnd.github.v3+json",
      Authorization: auth,
    },
  }).then((response) => {
    return response.json();
  });

  return json;
}
export async function sync(force) {
  if (!store.get("github")) return;
  let name = store.get("github").name;
  let rep = "data";
  let path = "links.json";
  let sinceName = "Since";
  let runAt = "runAt";

  let since = store.get(sinceName);
  let news = store.get("_n") || [];

  if (
    !force &&
    new Date().getTime() - 1000 * 3600 * 24 < (store.get(runAt) || 0) &&
    news.length < 10
  ) {
    return;
  }

  let contents = [];
  let commits = await forEachCommitsAsc(
    { name, rep, path, since },
    (commit, content, i) => {
      if (since && commit.commit.committer.date < since) return true;
      console.log(content, i);
      contents.push(content);
    },
    false
  );
  if (commits.length > 0) {
    contents.forEach((c) => {
      let links = JSON.parse(c);
      links.forEach((link) => {
        link.r = 1;

        save(link, 1);
      });
    });
    since = commits[0].commit.committer.date;
    store.set(sinceName, since);
  }

  let newItems = news.map((id) => getItem("links", id));

  if (newItems.length > 0) {
    let content = JSON.stringify(newItems);
    let json = await upload(name, rep, path, content);

    console.log(json);

    if (json.commit) {
      since = new Date(
        new Date(json.commit.committer.date).getTime() + 1000
      ).toISOString();
      newItems.forEach((e) => {
        e.r = 1;
        save(e);
      });
      store.set("_n", []);
      store.set(sinceName, since);
    }
  }
  store.set(runAt, new Date().getTime());
}

export async function tokenLogin(token) {
  let auth = "token " + token;

  return fetch("https://api.github.com/user", {
    method: "get",
    headers: {
      accept: "application/vnd.github.v3+json",
      Authorization: auth,
    },
  })
    .then((r) => r.json())
    .then((json) => {
      if (json.name) {
        store.set("github", json);
        localStorage.setItem("token", token);
      }
      return json;
    });
}
sync();
