<template>
  <div class="wrapper">
    <Header />
    <SideBar />
    <!--start content-->
    <main class="page-content">
      <div class="row">
        <div class="col-12 d-flex">
          <div class="card radius-10 w-100">
            <div class="card-body">
              <div class="d-flex align-items-center">
                <h6 class="mb-0">My Links</h6>
                <div class="fs-5 ms-auto d-flex dropdown">
                  <Tags />
                  <div
                    class="dropdown-toggle dropdown-toggle-nocaret cursor-pointer"
                    data-bs-toggle="dropdown"
                  >
                    <i class="bi bi-three-dots"></i>
                  </div>
                  <ul class="dropdown-menu">
                    <li><a class="dropdown-item" href="#">Action</a></li>
                    <li>
                      <a class="dropdown-item" href="#">Another action</a>
                    </li>
                    <li>
                      <hr class="dropdown-divider" />
                    </li>
                    <li>
                      <a class="dropdown-item" href="#">Something else here</a>
                    </li>
                  </ul>
                </div>
              </div>
              <div class="table-responsive mt-2">
                <table class="table align-middle mb-0">
                  <thead class="table-light">
                    <tr>
                      <th></th>
                      <th>#ID</th>
                      <th>title</th>
                      <td>time</td>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(link, i) in links" :key="link.url">
                      <td>
                        <input
                          type="checkbox"
                          :value="link.id"
                          v-model="selects"
                        />
                      </td>
                      <td>
                        {{ i + 1 }}
                      </td>
                      <td>
                        <a :href="link.url" target="_blank">
                          {{ link.title }}</a
                        >
                      </td>
                      <td>{{ link.t | fmtDate }}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!--end row-->
    </main>
    <!--end page main-->
    <!--start overlay-->
    <div class="overlay nav-toggle-icon"></div>
    <!--end overlay-->
    <!--Start Back To Top Button--><a href="javaScript:;" class="back-to-top"
      ><i class="bx bxs-up-arrow-alt"></i
    ></a>
    <!--End Back To Top Button-->
  </div>
</template>

<script>
import Header from "./components/Header.vue";
import SideBar from "./components/SideBar.vue";
import { save, search } from "./data";
import Tags from "./components/Tags.vue";

export default {
  name: "App",
  data() {
    return { links: search("links", 0).reverse(), selects: [] };
  },
  filters: {
    fmtDate(dateTime) {
      if (!dateTime) return "";
      const d = new Date(dateTime);
      const now = Date.now();

      const diff = (now - d) / 1000;

      if (diff < 30) {
        return "just";
      } else if (diff < 3600) {
        // less 1 hour
        return Math.ceil(diff / 60) + "m";
      } else if (diff < 3600 * 24) {
        return Math.ceil(diff / 3600) + "h";
      } else if (diff < 3600 * 24 * 2) {
        return "1d";
      }
      return d.getMonth() + 1 + "-" + d.getDate();
    },
  },
  mounted() {
    console.log(this);
    let href = location.href;
    if (href.indexOf("?") > -1 && href.split("=").length == 3) {
      let ps = href
        .split("?")[1]
        .split("&")
        .reduce((params, pair) => {
          params[pair.split("=")[0]] = decodeURIComponent(pair.split("=")[1]);
          return params;
        }, {});
      if (ps.title && ps.url) {
        ps["t"] = +new Date();

        save(ps);
        this.links.length = 0;
        this.links.push(...search("links", 0).reverse());
      }
    }
  },
  components: {
    Header,
    SideBar,
    Tags,
  },
};
</script>

